@use "../../style/colors" as *;

// DESKTOP NAVBAR
.Navbar {
  position: fixed;
  top: 0;
  left: 0;
  background-color: $white-color;
  width: 100%;
  padding: 9px 0;
  transition: 0.3s ease;
  z-index: 1000; //  AUMENTADO de 100 a 1000 para estar sobre el selector de idiomas
  
  // Estilos para el logo dentro del Navbar
  .navbar__logo {
    max-height: 120px;
    width: auto;
    transition: transform 0.3s ease;
    
    &:hover {
      transform: scale(1.05);
    }
  }

  .mobile__menu {
    display: none;
    padding: 10px 15px;
    cursor: pointer;
    z-index: 1001; // Mayor que el navbar
    
    img {
      transition: transform 0.3s ease;
      
      &:hover {
        transform: scale(1.1);
      }
    }
  }
  
  .desktop__menu {
    display: block;

    ul {
      display: flex;
      align-items: center;
      
      li {
        cursor: pointer;
        padding: 2rem 2.7rem;
        margin: 0 1rem;
        transition: all 0.3s ease;
      }
      
      li:last-child {
        margin-right: 0;
        padding-right: 0;
      }
      
      li:hover {
        color: $red-color;
      }
      
      //  NUEVO: Estilos para link activo
      .active-link {
        color: $red-color;
        font-weight: 700;
        position: relative;
        
        //  COMENTADO: Eliminar la lnea debajo del link activo
        // &::after {
        //   content: '';
        //   position: absolute;
        //   bottom: 1.5rem;
        //   left: 2.7rem;
        //   right: 2.7rem;
        //   height: 3px;
        //   background: $red-color;
        //   border-radius: 2px;
        // }
      }
    }
  }
}

.extraLargeNavbar {
  padding: 15px 0;
}

//  NUEVO: SELECTOR DE IDIOMA INTEGRADO EN NAVBAR (DESKTOP)
.navbar__language-selector {
  display: flex;
  gap: 0.3rem;
  align-items: center;
  margin-left: 1rem !important;
  padding: 1rem 1.5rem !important;
  border-left: 2px solid rgba(228, 104, 26, 0.2);
  
  .lang-btn {
    background: transparent;
    border: 2px solid transparent;
    border-radius: 50%;
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    padding: 0;
    
    .lang-flag {
      font-size: 1.3rem;
      transition: transform 0.3s ease;
    }
    
    &:hover {
      background: rgba(228, 104, 26, 0.1);
      transform: scale(1.15);
      
      .lang-flag {
        transform: scale(1.1);
      }
    }
    
    &.active {
      background: linear-gradient(135deg, #e4681a, #f5576c);
      border-color: transparent;
      box-shadow: 0 3px 12px rgba(228, 104, 26, 0.4);
      
      .lang-flag {
        transform: scale(1.1);
        filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
      }
    }
  }
}

// MOBILE NAVBAR
.mobile__navbar {
  height: 100vh;
  position: fixed;
  top: 0;
  right: -100%;
  background-color: #00000096;
  z-index: 1100; //  AUMENTADO de 1000 a 1100 para estar sobre todo
  width: 50%;
  transition: 0.4s ease;
  padding: 20px;
  overflow-y: auto;

  .mobile__navbar-close {
    padding: 20px;
    margin-right: 80%;
    cursor: pointer;
    transition: transform 0.3s ease;
    
    &:hover {
      transform: rotate(90deg);
    }

    img {
      min-width: 30px;
      height: auto;
    }
  }
  
  .mobile__navbar-logo {
    padding: 10px;
    cursor: pointer;
    text-align: center;

    img {
      max-width: 150px;
      height: auto;
      transition: transform 0.3s ease;
      
      &:hover {
        transform: scale(1.05);
      }
    }
  }
  
  .mobile__navbar-menu {
    padding-top: 20%;
    
    ul {
      li {
        cursor: pointer;
        color: $white-color;
        font-size: 2.5rem;
        padding: 5% 0;
        transition: all 0.3s ease;
      }
      
      li:hover {
        color: $main-color;
        transform: translateX(10px);
      }
      
      //  NUEVO: Estilos para link activo en mvil
      .active-link {
        color: $main-color;
        font-weight: 700;
      }

      //Estilos para el selector de idioma en mvil
      .navbar__language-selector-mobile {
        display: flex;
        justify-content: center;
        gap: 15px;  // Ms espacio entre botones para mviles
        margin-top: 30px;  // Espacio arriba para separarlo de los links
        padding: 15px 0;
        
        .lang-btn {
          background: rgba(245, 231, 187, 0.1);
          border: 1px solid transparent;
          border-radius: 50%;
          width: 50px;  // Ms grande que en desktop (36px)
          height: 50px;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          transition: all 0.3s ease;
          padding: 0;
          
          .lang-flag {
            font-size: 1.8rem;  // Ms grande que en desktop (1.3rem)
            transition: transform 0.3s ease;
          }
          
          &:hover {
            background: rgba(228, 104, 26, 0.1);
            transform: scale(1.1);
            
            .lang-flag {
              transform: scale(1.1);
            }
          }
          
          &.active {
            background: linear-gradient(135deg, #e4681a, #f5576c);
            border-color: transparent;
            box-shadow: 0 3px 12px rgba(228, 104, 26, 0.4);
            
            .lang-flag {
              transform: scale(1.1);
              filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
            }
          }
        }
      }
    }
  }
}

.mobile__open {
  right: 0;
}

// BACKDROP
.backdrop {
  width: 100%;
  height: 100vh;
  background-color: $black-color;
  opacity: 0.8;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1050; //  Entre navbar (1000) y men mvil (1100)
  display: none;
}

.backdrop__open {
  display: block;
}

// Lang switcher (si lo usas en algn lugar)
.lang-switcher {
  display: flex;
  gap: 0.4rem;
  margin-left: 1rem;

  button {
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;

    img {
      width: 28px;
      height: 20px;
      border-radius: 3px;
      opacity: 0.75;
      transition: opacity 0.3s ease;
    }

    img:hover {
      opacity: 1;
    }
  }
}

// MEDIA QUERIES
@media screen and (max-width: 968px) {
  //  NUEVO: Ocultar selector de idioma integrado en tablets/mviles
  .navbar__language-selector {
    display: none !important;
  }
}

@media screen and (max-width: 720px) {
  // Estilos para la barra de navegacin principal en mvil
  .Navbar {
    .mobile__menu {
      display: block;
    }
    
    .desktop__menu {
      display: none;
    }
  
    .navbar__logo {
      max-height: 50px; 
    }
  } 

  // Estilos para el men que se despliega
  .mobile__navbar {
    width: 70%;
    
    .mobile__navbar-menu {
      ul li {
        font-size: 2rem; // Un poco ms pequeo para mviles
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .Navbar {
    .navbar__logo {
      max-height: 45px;
    }
  }
  
  .mobile__navbar {
    width: 80%; // Ms ancho en pantallas muy pequeas
    
    .mobile__navbar-menu {
      padding-top: 15%;
      
      ul li {
        font-size: 1.8rem;
        padding: 4% 0;
      }
    }
    
    .mobile__navbar-logo img {
      max-width: 120px;
    }
  }
}

//  ACTUALIZADO: ANIMACIONES PARA LOS ITEMS DEL MEN MVIL (ahora 6 items)
@keyframes slideInFromLeft {
  from {
    transform: translateX(-30px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.mobile__navbar.mobile__open {
  .mobile__navbar-menu ul li {
    animation: slideInFromLeft 0.4s ease-out forwards;
    opacity: 0;
    
    @for $i from 1 through 6 {  //  ACTUALIZADO: Cambiado de 5 a 6 para cubrir los 5 links + 1 selector
      &:nth-child(#{$i}) {
        animation-delay: #{$i * 0.08}s;
      }
    }
  }
}